import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import axios from "axios";

interface JobState {
  jobs: any[]; // Replace 'any' with your Job type
  currentJob: any | null; // Replace 'any' with your Job type
  status: "idle" | "loading" | "succeeded" | "failed";
  error: string | null;
}

const initialState: JobState = {
  jobs: [],
  currentJob: null,
  status: "idle",
  error: null,
};

const API_URL = "https://akil-backend.onrender.com/opportunities/search";

export const fetchJobs = createAsyncThunk("jobs/fetchJobs", async () => {
  const response = await axios.get(API_URL);
  return response.data;
});

export const fetchJobById = createAsyncThunk(
  "jobs/fetchJobById",
  async (id: string) => {
    // Explicitly type the id parameter
    const response = await axios.get(
      `https://akil-backend.onrender.com/opportunities/${id}`
    );
    return response.data;
  }
);

const jobsSlice = createSlice({
  name: "jobs",
  initialState,
  reducers: {},
  extraReducers(builder) {
    builder
      .addCase(fetchJobs.pending, (state) => {
        state.status = "loading";
        state.error = null; // Clear previous errors
      })
      .addCase(fetchJobs.fulfilled, (state, action) => {
        state.status = "succeeded";
        state.jobs = action.payload;
      })
      .addCase(fetchJobs.rejected, (state, action) => {
        state.status = "failed";
        state.error = action.error.message || "Failed to fetch jobs"; // Fallback message
      })
      .addCase(fetchJobById.pending, (state) => {
        state.status = "loading";
        state.error = null; // Clear previous errors
      })
      .addCase(fetchJobById.fulfilled, (state, action) => {
        state.status = "succeeded";
        state.currentJob = action.payload;
      })
      .addCase(fetchJobById.rejected, (state, action) => {
        state.status = "failed";
        state.error = action.error.message || "Failed to fetch job details"; // Fallback message
      });
  },
});

export default jobsSlice.reducer;
